import login from '../components/common/login.vue';//
import logincode from '../components/common/logincode.vue';//
import register from '../components/common/register.vue';//
import forget from '../components/common/forget.vue';//
import agreement from '../components/common/agreement.vue';//
import index from '../components/index/index.vue';//
import main from '../components/index/main.vue';//
import car from '../components/index/car.vue';//
import order from '../components/index/order.vue';//
import mine from '../components/index/mine.vue';//
import moreHot from '../components/index/moreHot.vue';//
import chaneel from '../components/index/chaneel.vue';//
import list from '../components/list/list.vue';//
import search from '../components/search/search.vue';//
import goodsList from '../components/list/goodsList.vue';//
import goodsinfo from '../components/goods/goodsinfo.vue';//
import goodslease from '../components/goods/goodslease.vue';//
import authentication from '../components/mine/authentication.vue';//
import collection from '../components/mine/collection.vue';//
import setting from '../components/mine/setting.vue';//
import about from '../components/mine/about.vue';//
import help from '../components/mine/help.vue';//
import card from '../components/mine/card.vue';//
import opinion from '../components/mine/opinion.vue';//
import logistics from '../components/help/logistics.vue';//
import service from '../components/help/service.vue';//
import transaction from '../components/help/transaction.vue';//
import addressList from '../components/address/addressList.vue';//
import editAddress from '../components/address/editAddress.vue';//
import orderInfo from '../components/order/orderInfo.vue';//
import orderReturn from '../components/order/orderReturn.vue';//
import settlement from '../components/order/settlement.vue';//
import orderSuccess from '../components/order/orderSuccess.vue';//
import orderAction from '../components/order/orderAction.vue';//
import orderLogistics from '../components/order/orderLogistics.vue';//
import shop from '../components/shop/shop.vue';//
import shopInfo from '../components/shop/shopInfo.vue';//
import selected from '../components/shop/selected.vue';//
import download from '../components/common/download.vue';//App
import authInfo from '../components/antScore/authInfo.vue';//
import authPage from '../components/antScore/authPage.vue';//


const routes = [{
        path: '/index/:id',
        component: index,
        children:[
            {path:"",component:main},
            {path:"car",component:car},
            {path:"order", meta: {
                 requireAuth: true,  
                }, component:order},
            {path:"mine", meta: {
                // 
                 requireAuth: true,  
                }, component:mine},
        ]
    },{
        path: '/',
        redirect: '/index/main',
        name: 'index',
        component: index
    },
    {
        path: '/chaneel',
        name: 'chaneel',
        component: chaneel
    },
    {
        path: '/login',
        name: 'login',
        component: login
    },{
        path: '/logincode',
        name: 'logincode',
        component: logincode
    },{
        path: '/register',
        name: 'register',
        component: register
    },{
        path: '/forget',
        name: 'forget',
        component: forget
    },{
        path: '/list',
        name: 'list',
        component: list
    },{
        path: '/search',
        name: 'search',
        component: search
    },{
        path: '/goodsinfo/:id',
        name: 'goodsinfo',
        component: goodsinfo
    },{
        path: '/authInfo',
        name: 'authInfo',
        component: authInfo
    },{
        path: '/authPage',
        name: 'authPage',
        component: authPage
    },{
        path: '/goodslease/:id',
        name: 'goodslease',
        component: goodslease
    },
    {
        path: '/authentication',
        name: 'authentication',
        component: authentication
    },{
        path: '/help',
        name: 'help',
        component: help
    },
    {
        path: '/card',
        name: 'card',
        component: card
    },
    {
        path: '/opinion',
        name: 'opinion',
        component: opinion
    },{
        path: '/logistics',
        name: 'logistics',
        component: logistics
    },
    {
        path: '/agreement',
        name: 'agreement',
        component: agreement
    },
    {
        path: '/service',
        name: 'service',
        component: service
    },{
        path: '/transaction',
        name: 'transaction',
        component: transaction
    },{
        path: '/addressList',
        name: 'addressList',
        meta: {
            requireAuth: true,  
           },
        component: addressList
    },{
        path: '/editAddress/:id',
        name: 'editAddress',
        meta: {
            requireAuth: true,  
           },
        component: editAddress
    },{
        path:'/editAddress',
        meta: {
            requireAuth: true,  
           },
        redirect:'/editAddress/add'
    },{
        path:'/orderInfo/:id',
        meta: {
            requireAuth: true,  
           },
        name: 'orderInfo',
        component: orderInfo
    },{
        path:'/orderLogistics/:id',
        name: 'orderLogistics',
        component: orderLogistics
    },{
        path:'/orderReturn',
        name: 'orderReturn',
        component: orderReturn
    },
    {
        path:'/goodsList/',
        name: 'goodsList',
        component: goodsList
    },{
        path:'/collection',
        name: 'collection',
        component: collection
    },{
        path:'/setting',
        name: 'setting',
        component: setting
    },{
        path:'/about',
        name: 'about',
        component: about
    },{
        path:'/shop/:id',
        name: 'shop',
        component: shop
    },{
        path:'/shopInfo/:id',
        name: 'shopInfo',
        component: shopInfo
    },
    {
        path:'/selected',
        name: 'selected',
        component: selected
    },
    {
        path:'/download',
        name: 'download',
        component: download
    },{
        path:'/orderAction/:id',
        name: 'orderAction',
        component: orderAction
    },{
        path:'/orderSuccess',
        name: 'orderSuccess',
        component: orderSuccess
    },{
        path:'/settlement',
        name: 'settlement',
        component: settlement
    },{
        path:'/moreHot',
        name: 'moreHot',
        component: moreHot
    }
];

    

export default new Router({
    mode: 'hash',
    routes
}); 


